
.container{
	max-width: 90%;
	margin:auto;
}
.row{
	display: flex;
	flex-wrap: wrap;
}
ul{
	list-style: none;
}
#footer{
	background-color: #0a72ca !important;
    padding: 70px 0;
}
.footer-col{
   width: 25%;

}
.footer-col h4{
	font-size: 18px;
    width: 175px;
	color: #ffc243;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
	cursor: pointer;
}
.footer-col img{
width: 125px;
}
.footer-col h4::before{
	content: '';
	position: absolute;
	left:0;
	bottom: -10px;
	background-color: #e91e63;
	height: 2px;
	box-sizing: border-box;
	width: 0%;
	transition: 2s all;
}
.footer-col h4:hover::before{
	width: 100%;
}
.footer-col ul li:not(:last-child){
	margin-bottom: 10px;
}
.footer-col ul li a{
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	text-decoration: none;
	font-weight: 300;
	color: #bbbbbb;
	display: block;
	transition: all 0.3s ease;
}
.footer-col ul li a:hover{
	color: #ffffff;
	padding-left: 8px;
}
.sp{
	display: flex;
	justify-content: center;
	align-items: center;
}
.footer-col .social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	background-color: rgba(255,255,255,0.2);
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: #ffffff;
	transition: all 0.5s ease;
}
.footer-col .social-links a:hover{
	color: #24262b;
	background-color: #ffffff;
}

/*responsive*/
@media(max-width: 767px){
  .footer-col{
    width: 50%;
    margin-bottom: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

}
@media(max-width: 574px){
  .footer-col{
    width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
}
